.projects-grid{
  display: flex;    
}



.avater-img{
height: 250px;
padding-right: 40%;
padding-left: 40%;
border-radius: 50%;
}

.banner-text{
  background-color: #000;
  opacity: .8;
  width: 75%;
  margin: auto;
  border-radius: 10px;
}


.banner-text h1{
  font-size: 66px;
  font-weight: bold;
  color : white;
  text-align: center;
}

.banner-text hr{
  border-top: 5px dotted white;
  width: 50%;
  margin: auto; 
}

.banner-text p{
  color: aliceblue;
  font-size: 20px;
  padding: 1em; 
  text-align: center;
}

.social-links{
  display: flex;
  justify-content: space-between;
  width: 50%;
  margin: auto;
}

.social-links i {
  color: white;
  font-size: 5em;
  padding: 8px;
}

/*
Contact page CSS
*/

.contact-body{
  margin: auto;
  background: grey;  
  position: fixed;
  width: 100%;
  height: 100%;
  justify-content: center;
  padding-top: 2em;
}

.contact-grid{
  text-align: center;
  justify-content: center;
  width: 80%;
  height: 80%;
  background: white;
  color: black;
  margin-bottom: 20px;
}

.contact-grid h2{
  font-family: 'Anton', sans-serif;
}

.contact-grid p {
  font-family: 'Oxygen', sans-serif;
  font-weight: bold;
}

.contact-grid hr{
  border-top: 5px dotted black;
  width: 50px;
  margin: auto;
}

.contact-list i{
  font-size : 50px;
  padding-right: 2rem;
}

